	module.exports = {"data":{"post":{"html":"<p><a title=\"BuzzMachine... by Jeff Jarvis\" href=\"http://www.buzzmachine.com/archives/2003_09.html#004630\">BuzzMachine</a> (on travelling to the memorial service yesterday): \"The subway -- the 1 -- pulls past the closed World Trade Center stop and, as it always does, it slows as if in respect\"</p>\n<p>The subways in Berlin used to do the same at the boarded-up stations on the east side of the wall, as if they had hope against hope that a passenger would somehow be waiting at the platform.</p>\n<p>Those stations are open now. It may take years, but hope sometimes wins out.</p>\n<p>(originally left as a comment at Buzz Machine)</p>","fields":{"title":"Hope Against Hope","date":"12 Sep 2003","url":"https://papascott.de/2003-09-12-hope-against-hope/"}}},"pathContext":{"slug":"/2003-09-12-hope-against-hope/","prev":{"fields":{"title":"Time Stood Still","date":"11 Sep 2003","slug":"/2003-09-11-time-stood-still/"}},"next":{"fields":{"title":"Hurt","date":"12 Sep 2003","slug":"/2003-09-12-hurt/"}}}}

